import microBlog from "../../components/microBlog/microBlog.vue"
import loading from "../../components/loading/loading.vue"
import tabs from "../../components/tabs/tabs.vue"
const apiPromise = require('../../static/utils/Promise.js');

export default {
	components: { microBlog, loading, tabs },
	data() {
		return {
			blog: "",					//别人发的微博
			blog2: "",
			promotional: "",			//推荐视频信息
			loadingText: "加载中...", 	//加载组件显示的文本
			showGroup: false,			//控制顶部分组的显示
			tip: 1,						//控制顶部分组和推荐按钮的切换
			editable: false,			//编辑分组开关
			clickGroup: 1,				//选中的分组的id
			group: [					//默认分组
				{"id":1,"text":"全部关注"},
				{"id":2,"text":"最新微博"},
				{"id":3,"text":"特别关注"},
				{"id":4,"text":"好友圈"},
				{"id":5,"text":"原创"},
				{"id":6,"text":"视频"},
				{"id":7,"text":"V+微博"},
				{"id":8,"text":"群微博"},
			],
			group2: ""					//自定义分组
		}
	},
	onLoad() {
		//登录判断
		// var res = global.isLogin();
		var res = true
		if(!res){
		    uni.showModal({
				title:'请登录',
				content:"请登录",
				success:function(){
					if (res.confirm) {
					    console.log('用户点击确定');
						uni.navigateTo({
							url:"/pages/login/login"
						});
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			})
		}else {
			//获取推荐视频信息
			apiPromise.Get('/promotionalVideo').then((res) => {
				console.log("推荐视频信息",res.data);
				this.promotional = res.data
			}).catch((err) => {
				console.log("获取推荐视频超时",err)
			})
			//获取关注人的微博
			apiPromise.Post('/microBlog').then((res) => {
				console.log("微博数据",res.data);
				let data = res.data,i ;
				for(i = 0; i < data.length; i++) {
					data[i].showTranspond = false
				}
				this.blog = data
			}).catch((err) => {
				console.log("获取微博超时",err)
				uni.showToast({
				    title: '获取微博超时,请刷新重试',
				    duration: 2000
				});
			})
		}	
	},
	onReachBottom() {
		console.log("到底了")
		if(this.tip == 1) {
			//获取关注人的微博
			apiPromise.Post('/microBlog').then((res) => {
				console.log("微博数据",res.data);
				this.blog = this.blog.concat(res.data)
			}).catch((err) => {
				console.log("失败",err)
			})
		} else {
			//获取关注人的微博
			apiPromise.Post('/microBlog2').then((res) => {
				console.log("微博数据",res.data);
				this.blog2 = this.blog2.concat(res.data)
			}).catch((err) => {
				console.log("失败",err)
			})
		}
	},
	methods: {
		showGrouping() {
			//获取我的分组
			if(!this.showGroup){
				if(this.group2 == "") {
					apiPromise.Post('/group').then((res) => {
						console.log("我的分组",res.data);
						this.group2 = res.data	
					}).catch((err) => {
						console.log("失败",err)
					})	
				}
				this.showGroup = true
			} else {
				this.showGroup = false
				this.editable = false
			}
			
		},
		selectGroup(id) {
			if(!this.editable) {
				this.clickGroup = id
				this.showGroup = false
				this.editable = false
			}		
		},
		//关注和推荐界面切换
		switchTip(tip){
			this.showGroup = false
			this.editable = false
			if(tip == 1) {
				this.tip = 1
			}else {
				if(this.blog2 == "") {
					//获取推荐的微博
					apiPromise.Post('/microBlog2').then((res) => {
						console.log("微博数据",res.data);
						let data = res.data,i ;
						for(i = 0; i < data.length; i++) {
							data[i].showTranspond = false
						}
						this.blog2 = data
					}).catch((err) => {
						console.log("获取微博超时",err)
						uni.showToast({
						    title: '获取微博超时,请刷新重试',
						    duration: 2000
						});
					})	
				}
				this.tip = 2
			}
		},
		//编辑分组
		edit() {
			this.editable = !this.editable
		},
		showTranSpond(params) {
		    this.blog[params].showTranspond = true
		},
		deleteGroup(id) {
			const that = this
			uni.showModal({			    
			    content: '确定删除该分组吗？',
				confirmColor: '#fb8e1f',
			    success: function (res) {
			        if (res.confirm) {
			            uni.showLoading({
			                title: '删除中'
			            });
						let group = that.group2,
							len = group.length
						for (let i = 0; i<len; i++) {
							if(group[i].id == id) {
								group.splice(i,1);
								break
							}
						}
						setTimeout(function(){uni.hideLoading()}, 1000)							
			        }
			    }
			});
		}
	}
}
